* {
    margin: 0;
    padding: 0;
}

body {
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.wrapper {
    position: relative;
    width: 100%;
    height: 100vh;
    background: url(../img/bg_img.jpg) no-repeat center;
    background-size: cover;

    &-container {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        color: #ffffff;
    }
}

    .scroll_section {
        width: 100%;
        height: 100vh;
        background: #eeeeee;
    }


